import React, {Component} from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { createStore } from 'redux';
import { Provider } from 'react-redux';

import styles from './resources/styles/styles';
import reducers from './store/reducers';

import Splash from './screens/Splash';
import Start from './screens/Start';
import Confirm from './screens/Confirm';
import TakePics from './screens/TakePics';
import GetLocation from './screens/GetLocation';
import Progress from './screens/Progress';
import Numbers from './screens/Numbers';

const store = createStore(reducers);

class App extends Component {
  render() {
    return (
      <div style={{backgroundColor: styles.system_background_color}}>

        <Provider store={store}>
          <Router>
            <div>
              <Route exact path="/" component={Splash} />
              <Route path="/start" component={Start} />
              <Route path="/confirm" component={Confirm} />
              <Route path="/take_pics" component={TakePics} />
              <Route path="/get_location" component={GetLocation} />
              <Route path="/progress" component={Progress} />
              <Route path="/numbers" component={Numbers} />
            </div>
          </Router>
        </Provider>
      </div>
    );
  }
}

export default App;



