const system_background_color = '#E01F26';
const system_positive_color = '#4CAF50';
const system_negative_color = '#333030';
const system_font_color = '#FFF';

const styles = {
  system_background_color: system_background_color,
  buttons_container: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'center',
  },
  system_positive_button: {
    backgroundColor: system_positive_color,
    padding: 20,
    width: '20%',
    borderRadius: 10,
  },
  system_negative_button: {
    backgroundColor: system_negative_color,
    padding: 20,
    width: '20%',
    borderRadius: 10,
  },
  system_link: {
    color: system_font_color,
    fontSize: 30,
    textDecoration: 'none',
    padding: 20,
    width: '20%',
    borderRadius: 10,
  },
  positive_link: {
    backgroundColor: system_positive_color,
    color: system_font_color,
    fontSize: 30,
    textDecoration: 'none',
    padding: 20,
    borderRadius: 10,
    margin: 10,
  },
  negative_link: {
    backgroundColor: system_negative_color,
    color: system_font_color,
    fontSize: 30,
    textDecoration: 'none',
    padding: 20,
    borderRadius: 10,
    margin: 10,
},
  system_screen_container: {
    display: 'flex',
    minHeight: '100vh',
    flexDirection: 'column',
    textAlign: 'center',
    backgroundColor: system_background_color,
    color: system_font_color,
  },
  badge: {
    width: 50,
    height: 50,
    borderRadius: '50%',
    fontSize: 30,
    color: '#fff',
    lineHeight: 1.5,
    textAlign: 'center',
    background: '#bbb',
  },
  over_links_gap: {
    height: 50,
  }
};

export default styles;



