import constants from '../constants/language_constants';

export const _language_change_to_en = () => {
  return {
    type: constants._LANGUAGE_CHANGE_TO_EN,
    payload: null
  };
};
export const _language_change_to_ar = () => {
  return {
    type: constants._LANGUAGE_CHANGE_TO_AR,
    payload: null
  };
};
export const _language_change_to_fr = () => {
  return {
    type: constants._LANGUAGE_CHANGE_TO_FR,
    payload: null
  };
};
export const _language_change_to_ur = () => {
  return {
    type: constants._LANGUAGE_CHANGE_TO_UR,
    payload: null
  };
};


