import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import messages from '../resources/messages/messages';
import Header from '../parts/Header';
import styles from '../resources/styles/styles';
import loading from '../resources/images/loading2.gif';
import logo from '../resources/images/logo2.png';

//import Start from './Start';

class _Component extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false
    };

  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        redirect: true
      })
    }, 3000);
  }

  render() {
    const RenderRedirect = () => {
      if (this.state.redirect) {
        return (
          <div style={styles.buttons_container}>
            <Link style={styles.positive_link} to={`/start`}>{messages[this.props.language.code].next}</Link>
          </div>
        )
      } else {
        return <img style={{}} src={loading} alt="loading" />
      }
    };

    return (
      <div style={ styles.system_screen_container }>
        <Header/>
        <div style={{
          flex: 1,
          justifyContent: 'center',
          alignItems: 'center',
          width: '100%',
          height: '100%',
        }}>

          <img style={{
            marginTop: 30,
            width: 300,
            height: 300,
          }} src={logo} alt="logo" />
        </div>
        <div style={styles.buttons_container}>
          <div><h1><span>طارئ</span> - <span>Taree</span></h1></div>
        </div>

        <div style={{
          flex: 1,
          justifyContent: 'center',
          alignItems: 'center',
          width: '100%',
        }}>
          <RenderRedirect/>
        </div>

      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language,
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



