import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
//import { Link } from 'react-router-dom';

import Header from '../parts/Header';
import styles from "../resources/styles/styles";
import messages from "../resources/messages/messages";

class _Component extends Component {
  render() {
    return (
      <div style={ styles.system_screen_container }>
        <Header/>
        <h1>{messages[this.props.language.code].helpline}</h1>
        <h2>{messages[this.props.language.code].red_crescent}</h2>
        <h2>{messages[this.props.language.code].police}</h2>
        <h2>{messages[this.props.language.code].civil_defence}</h2>
        <h2>{messages[this.props.language.code].trafic_control}</h2>
        <h2>{messages[this.props.language.code].roads_secuity}</h2>
      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



