import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import messages from '../resources/messages/messages';
import Header from '../parts/Header';
import styles from "../resources/styles/styles";

class _Component extends Component {

  constructor(props){
    super(props);
    this.state = {
      constraints: { audio: false, video: { width: 200, height: 150 } }
    };
    this.handleStartClick = this.handleStartClick.bind(this);
    this.takePicture = this.takePicture.bind(this);
    this.clearPhoto = this.clearPhoto.bind(this);
  }

  componentDidMount(){
    const constraints = this.state.constraints;
    const getUserMedia = (params) => (
      new Promise((successCallback, errorCallback) => {
        navigator.webkitGetUserMedia.call(navigator, params, successCallback, errorCallback);
      })
    );

    getUserMedia(constraints)
      .then((stream) => {
        const video = document.querySelector('video');
        const vendorURL = window.URL || window.webkitURL;

        video.src = vendorURL.createObjectURL(stream);
        video.play();

        //video.pause();
        //stream.stop();
      })
      .catch((err) => {
        console.log(err);
      });

    this.clearPhoto();
  }

  clearPhoto(){
    const canvas = document.querySelector('canvas');
    const photo = document.getElementById('photo');
    const context = canvas.getContext('2d');
    const { width, height } = this.state.constraints.video;
    context.fillStyle = '#FFF';
    context.fillRect(0, 0, width, height);

    const data = canvas.toDataURL('image/png');
    photo.setAttribute('src', data);
  }

  handleStartClick(event){
    event.preventDefault();
    this.takePicture();
  }

  takePicture(){
    const canvas = document.querySelector('canvas');
    const context = canvas.getContext('2d');
    const video = document.querySelector('video');
    const photo = document.getElementById('photo');
    const { width, height } = this.state.constraints.video;

    canvas.width = width;
    canvas.height = height;
    context.drawImage(video, 0, 0, width, height);

    const data = canvas.toDataURL('image/png');
    photo.setAttribute('src', data);
  }

  render() {
    const Camera = (props) => (
      <div style={{flex: 1, flexDirection: 'row'}}>
        <video id="video" style={ styles.picSize }></video>
        <a id="startButton"
           onClick={ props.handleStartClick }
           style={ styles.button }
        >Take photo</a>
      </div>
    );


    return (
      <div style={ styles.system_screen_container }>
        <Header/>

        <h1>{messages[this.props.language.code].take_photos}</h1>

        <Camera
          handleStartClick={ this.handleStartClick }
        />
        <canvas id="canvas"
                style={ styles.picSize }
                hidden
        ></canvas>
        <div className="output"
             style={ styles.box }
        >
          <img id="photo" alt='taken'
               style={ styles.picSize }
          />
          <a id="saveButton"
             onClick={ this.handleSaveClick }
             style={ styles.button }
          >Save Photo</a>
        </div>
        <br/>

        <div style={styles.buttons_container}>
          <Link style={styles.positive_link} to={`/progress`}>{messages[this.props.language.code].next}</Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language,
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



