import objectAssign from 'object-assign';
import constants from '../constants/language_constants';

const init = {
  display: 'English',
  code: 'en',
  dir: 'ltr',
  isDirLeft: true,
};

export default (state=init, action)=>{
  switch (action.type) {
    case constants._LANGUAGE_CHANGE_TO_EN:
      return objectAssign({}, state,
        {
          display: 'English',
          code: 'en',
          dir: 'ltr',
          text_align: 'left',
          isDirLeft: true,
        });
    case constants._LANGUAGE_CHANGE_TO_AR:
      return objectAssign({}, state,
        {
          display: 'عربي',
          code: 'ar',
          dir: 'rtl',
          text_align: 'right',
          isDirLeft: false,
        });
    case constants._LANGUAGE_CHANGE_TO_FR:
      return objectAssign({}, state,
        {
          display: 'Français',
          code: 'fr',
          dir: 'ltr',
          text_align: 'left',
          isDirLeft: true,
        });
    case constants._LANGUAGE_CHANGE_TO_UR:
      return objectAssign({}, state,
        {
          display: 'اردو',
          code: 'ur',
          dir: 'rtl',
          text_align: 'right',
          isDirLeft: false,
        });
    default:
      return state;
  }
}



