import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import styles from "../resources/styles/styles";
import {
  _language_change_to_ar,
  _language_change_to_en,
  _language_change_to_fr,
  _language_change_to_ur,
} from "../store/actions/language_actions";


class _Component extends Component {

  render() {
    return (
      <div style={{
        backgroundColor: styles.system_background_color,
      }}>
        <select
          value={this.props.language.code}
          onChange={(event)=>{
              switch (event.target.value) {
                case 'ar':
                  this.props._language_change_to_ar();
                  break;
                case 'fr':
                  this.props._language_change_to_fr();
                  break;
                case 'ur':
                  this.props._language_change_to_ur();
                  break;
                case 'en':
                  this.props._language_change_to_en();
                  break;
                default:
                  break;
              }
          }}
        >
          <option value="en">English</option>
          <option value="ar">عربي</option>
          <option value="fr">Français</option>
          <option value="ur">اردو</option>
        </select>

      </div>
    )
  }
}

const mapStateToProps = state =>({
  language: state.language
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
      _language_change_to_ar,
      _language_change_to_en,
      _language_change_to_fr,
      _language_change_to_ur,
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



