import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
//import { Link } from 'react-router-dom';

import Header from '../parts/Header';
import styles from "../resources/styles/styles";
import messages from '../resources/messages/messages';

class _Component extends Component {
  constructor(props){
    super(props);
    this.state = {
      error: null,
      step: 'get_case_id',
      steps: [],
    }
  }

  componentDidMount(){
    this.step_get_case_id()
  }

  step_get_case_id() {
    fetch("http://10.0.0.212/api/caseId")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            step: 'get_location',
            steps: this.state.steps.push('Get Location'),
            case_id: result,
          });
          this.step_get_location();
        },
        (error) => {
          this.setState({
            error: error,
          });
        }
      );
  }

  step_get_location() {
    const parent = this;
    navigator.geolocation.getCurrentPosition(
      value => {
        console.log(value.coords);
        parent.setState({
          step: 'send_location',
          steps: this.state.steps.push('Send Location'),
          location: value.coords,
        });
        this.step_send_location();
      },
      error => {
        parent.setState({
          error: error,
        });
      }
    );
  }

  step_send_location() {
    fetch(`http://10.0.0.212/api/Location/${this.state.case_id}/${this.state.location.longitude.toString().replace('.', '_')}/${this.state.location.latitude.toString().replace('.', '_')}`)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            step: 'contact_units',
            steps: this.state.steps.push('Contact units'),
          });
        },
        (error) => {
          this.setState({
            error: error,
          });
        }
      );
  }

  step_contact_units(){

  }



  render() {
    return (
      <div style={{
        ...styles.system_screen_container,
        direction: this.props.language.dir,
      }}>
        <Header/>
        <h3 style={{ color: 'red' }}>{this.state.error}</h3>
        <h3>case id {this.state.case_id}</h3>
        <h1>{messages[this.props.language.code].please_wait}</h1>
        <h2>Step {this.state.step}</h2>
        <div style={{textAlign: this.props.language.text_align, width: 500}}>
          <h2 style={{textAlign: this.props.language.text_align, width: 500}}><span>&#x25C9;</span> <span>{messages[this.props.language.code].contacting_units}</span></h2>
          <h2 style={{textAlign: this.props.language.text_align, width: 500}}><span>&#x25EF;</span> <span>{messages[this.props.language.code].request_received}</span></h2>
          <h2 style={{textAlign: this.props.language.text_align, width: 500}}><span>&#x25EF;</span> <span>{messages[this.props.language.code].help_on_way}</span></h2>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language,
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);




