import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import Header from '../parts/Header';
import styles from "../resources/styles/styles";
import messages from '../resources/messages/messages';

class _Component extends Component {
  constructor(props){
    super(props);
    this.state={
      status: <div> </div>,
    };
  }
  render() {
    const parent = this;
    navigator.geolocation.getCurrentPosition(
      value=>{
        parent.setState({
          status:
            <div>
              <p><span>{messages[this.props.language.code].longitude}</span> <span>{value.coords.longitude}</span></p>
              <p><span>{messages[this.props.language.code].latitude}</span> <span>{value.coords.latitude}</span></p>
            </div>
        });
      },
      error=>{
        parent.setState({
          status:
            <div>
              <p>{messages[this.props.language.code].error}</p>
              <p>{error.message}</p>
            </div>
        });
      }
    );
    return (
      <div style={ styles.system_screen_container }>
        <Header/>
        <h1>{messages[this.props.language.code].locate_incident}</h1>
        <h3>{this.state.status}</h3>
        <div style={styles.over_links_gap}> </div>
        <div style={styles.buttons_container}>
          <Link style={styles.positive_link} to={`/progress`}>{messages[this.props.language.code].send}</Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language,
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



