import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import Header from '../parts/Header';
import styles from "../resources/styles/styles";
import messages from "../resources/messages/messages";

class _Component extends Component {
  render() {
    return (
      <div style={ styles.system_screen_container }>
        <Header/>
        <h1>{messages[this.props.language.code].make_sure_title}</h1>
        <h2>{messages[this.props.language.code].make_sure_option_1}</h2>
        <h2>{messages[this.props.language.code].make_sure_option_2}</h2>

        <div style={styles.over_links_gap}> </div>
        <div style={{
          flex: 1,
          justifyContent: 'center',
          alignItems: 'center',
          width: '100%',
          height: '100%',
        }}>
          <div style={styles.buttons_container}>
            <Link style={styles.positive_link} to={`/take_pics`}>{messages[this.props.language.code].yes}</Link>
            <Link style={styles.negative_link} to={`/numbers`}>{messages[this.props.language.code].no}</Link>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state =>({
  language: state.language
});
const matchDispatchToProps = dispatch =>(
  bindActionCreators({
    }
    , dispatch)
);
export default connect(mapStateToProps, matchDispatchToProps)(_Component);



